
<!--popover 组件




 -->
<template>
  <div class="popover" ref="popover">
    <div
      ref="contentWrapper"
      class="gulu-popover-content-wrapper"
      v-if="visible"
      :class="[{[`position-${position}`]:true}, popClassName]"
    >
      <!-- 具名内容插槽 -->
      <slot name="content" :close="close">内容</slot>
    </div>
    <div ref="triggerWrapper" style="display: inline-block;">
      <!-- 默认插槽 -->
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: "hPopover",
  props: {
    popClassName: {
      type: String
    },
    // 出现方式
    position: {
      type: String,
      default: "top",
      validator(value) {
        return ["top", "bottom", "left", "right"].indexOf(value) >= 0;
      }
    },
    // 触发方式
    trigger: {
      type: String,
      default: "click",
      validator(value) {
        return ["click", "hover"].indexOf(value) >= 0;
      }
    },
    //
    container: {
      type: Element
    }
  },
  data() {
    return {
      visible: false
    };
  },

  mounted() {
    this.addPopoverListeners();
  },
  beforeDestroy() {
    this.putBackContent();
    this.removePopoverListeners();
  },
  computed: {
    openEvent() {
      if (this.trigger === "click") {
        return "click";
      } else {
        return "mouseenter";
      }
    },
    closeEvent() {
      if (this.trigger === "click") {
        return "click";
      } else {
        return "mouseleave";
      }
    }
  },
  methods: {
    // 添加事件监听
    addPopoverListeners() {
      if (this.trigger === "click") {
        this.$refs.popover.addEventListener("click", this.onClick);
      } else {
        this.$refs.popover.addEventListener("mouseenter", this.open);
        this.$refs.popover.addEventListener("mouseleave", this.close);
      }
    },
    //移除事件监听
    removePopoverListeners() {
      if (this.trigger === "click") {
        this.$refs.popover.removeEventListener("click", this.onClick);
      } else {
        this.$refs.popover.removeEventListener("mouseenter", this.open);
        this.$refs.popover.removeEventListener("mouseleave", this.close);
      }
    },
    //
    putBackContent() {
      const { contentWrapper, popover } = this.$refs;
      if (!contentWrapper) {
        return;
      }
      popover.appendChild(contentWrapper);
    },
    //根据点击的dom，计算应该出现在什么地方
    positionContent() {
      const { contentWrapper, triggerWrapper } = this.$refs;
      (this.container || document.body).appendChild(contentWrapper);
      const {
        width,
        height,
        top,
        left
      } = triggerWrapper.getBoundingClientRect();
      const { height: height2 } = contentWrapper.getBoundingClientRect();
      let positions = {
        top: { top: top + window.scrollY, left: left + window.scrollX },
        bottom: {
          top: top + height + window.scrollY,
          left: left + window.scrollX
        },
        left: {
          top: top + window.scrollY + (height - height2) / 2,
          left: left + window.scrollX
        },
        right: {
          top: top + window.scrollY + (height - height2) / 2,
          left: left + window.scrollX + width
        }
      };
      contentWrapper.style.left = positions[this.position].left + "px";
      contentWrapper.style.top = positions[this.position].top + "px";
    },
    // 点击网页时
    onClickDocument(e) {
    
      if (
        this.$refs.popover &&
        (this.$refs.popover === e.target ||
          this.$refs.popover.contains(e.target))
      ) {
        return;
      }
      if (
        this.$refs.contentWrapper &&
        (this.$refs.contentWrapper === e.target ||
          this.$refs.contentWrapper.contains(e.target))
      ) {
        return;
      }
      this.close();
    },
    // 展示
    open() {
      this.visible = true;
      this.$emit("open");
      this.$nextTick(() => {
        this.positionContent();
        document.addEventListener("click", this.onClickDocument);
        window.addEventListener("resize", this.positionContent);
      });
    },
    // 关闭
    close() {
      this.visible = false;
      this.$emit("close");
      document.removeEventListener("click", this.onClickDocument);
      window.removeEventListener("resize", this.positionContent);
    },
    // 点击时触发
    onClick(event) {
      if (this.$refs.triggerWrapper.contains(event.target)) {
        if (this.visible === true) {
          this.close();
        } else {
          this.open();
        }
      }
    }
  }
};
</script>

<style scoped lang="stylus">
$border-color = #ccc;
$border-radius = 4px;

.popover {
  display: inline-block;
  vertical-align: top;
  position: relative;
}

.gulu-popover-content-wrapper {
  z-index: 999;
  position: absolute;
  border: 1px solid #fff;
  border-radius: $border-radius;
  background: white;
  padding: 0.5em 1em;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  word-break: break-all;

  &::before, &::after {
    content: '';
    display: block;
    border: 10px solid transparent;
    width: 0;
    height: 0;
    position: absolute;
  }

  /* *四种定位模式 */
  &.position-top {
    transform: translateY(-100%);
    margin-top: -10px;

    &::before, &::after {
      left: 10px;
    }

    &::before {
      border-top-color: white;
      border-bottom: none;
      top: 100%;
    }

    &::after {
      border-top-color: white;
      border-bottom: none;
      top: calc(100% - 1px);
    }
  }

  &.position-bottom {
    margin-top: 10px;

    &::before, &::after {
      left: 10px;
    }

    &::before {
      border-top: none;
      border-bottom-color: white;
      bottom: 100%;
    }

    &::after {
      border-top: none;
      border-bottom-color: white;
      bottom: calc(100% - 1px);
    }
  }

  &.position-left {
    transform: translateX(-100%);
    margin-left: -10px;

    &::before, &::after {
      transform: translateY(-50%);
      top: 50%;
    }

    &::before {
      border-left-color: white;
      border-right: none;
      left: 100%;
    }

    &::after {
      border-left-color: white;
      border-right: none;
      left: calc(100% - 1px);
    }
  }

  &.position-right {
    margin-left: 10px;

    &::before, &::after {
      transform: translateY(-50%);
      top: 50%;
    }

    &::before {
      border-right-color: white;
      border-left: none;
      right: 100%;
    }

    &::after {
      border-right-color: white;
      border-left: none;
      right: calc(100% - 1px);
    }
  }
}
</style>
